<template>

  <div class="animated fadeIn">
    <b-card header-tag="header">
      <div slot="header">

        <b-row>
            
          <b-col>
            <i class="icon-drop"></i> TestTable
          </b-col>
          <b-col class="text-right">
              
            <b-dropdown text="View Source" class="m-0 p-0">
              <b-dropdown-item class="p-0" v-for="(x,index) in [
                `/src/components/ComTest1/TestTable.vue`,
                `/src/_nav.js`,
                '/src/store/Count.js',
              ]" :key="index">
                  <com-vs2 :file-path-link=" x " ></com-vs2>
              </b-dropdown-item>
            </b-dropdown>
            
          </b-col>
          
        </b-row>



      </div>
      <b-card-body>

        <div>
          <!-- User Interface controls -->
          <b-row>
            <b-col md="6" class="my-1">
              <b-form-group label-cols-sm="3" label="Filter" class="mb-0">
                <b-input-group>
                  <b-form-input size="sm" v-model="filter" placeholder="Type to Search"></b-form-input>
                  <b-input-group-append>
                    <b-button size="sm" :disabled="!filter" @click="filter = ''">Clear</b-button>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>

            <b-col md="6" class="my-1">
              <b-form-group label-cols-sm="3" label="Sort" class="mb-0">
                <b-input-group>
                  <b-form-select size="sm" v-model="sortBy" :options="sortOptions">
                    <option slot="first" :value="null">-- none --</option>
                  </b-form-select>
                  <b-form-select size="sm" v-model="sortDesc" :disabled="!sortBy" slot="append">
                    <option :value="false">Asc</option>
                    <option :value="true">Desc</option>
                  </b-form-select>
                </b-input-group>
              </b-form-group>
            </b-col>

            <b-col md="6" class="my-1">
              <b-form-group label-cols-sm="3" label="Sort direction" class="mb-0">
                <b-form-select size="sm" v-model="sortDirection">
                  <option value="asc">Asc</option>
                  <option value="desc">Desc</option>
                  <option value="last">Last</option>
                </b-form-select>
              </b-form-group>
            </b-col>

            <b-col md="6" class="my-1">
              <b-form-group label-cols-sm="3" label="Per page" class="mb-0">
                <b-form-select size="sm" v-model="perPage" :options="pageOptions"></b-form-select>
              </b-form-group>
            </b-col>
          </b-row>

          <!-- Main table element -->
          <b-table
            show-empty
            stacked="md"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered"
          >
            <template slot="name" slot-scope="row">{{ row.value.first }} {{ row.value.last }}</template>

            <template slot="isActive" slot-scope="row">{{ row.value ? 'Yes :)' : 'No :(' }}</template>

            <template slot="actions" slot-scope="row">
              <b-button
                size="sm"
                @click="info(row.item, row.index, $event.target)"
                class="mr-1"
              >Info modal</b-button>
              <b-button
                size="sm"
                @click="row.toggleDetails"
              >{{ row.detailsShowing ? 'Hide' : 'Show' }} Details</b-button>
            </template>

            <template slot="cmd" slot-scope="row">
              <button @click="on_edit(row)">
                <i class="fa fa-pencil" aria-hidden="true"></i>
              </button>
              <button @click="on_del(row)">
                <i class="fa fa-trash" aria-hidden="true"></i>
              </button>
            </template>

            <template slot="row-details" slot-scope="row">
              <b-card>
                <ul>
                  <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
                </ul>
              </b-card>
            </template>
          </b-table>

          <b-row>
            <b-col md="6" class="my-1">
              <b-pagination
                v-model="currentPage"
                :total-rows="totalRows"
                :per-page="perPage"
                class="my-0"
              ></b-pagination>
            </b-col>
          </b-row>

          <!-- Info modal -->
          <b-modal :id="infoModal.id" :title="infoModal.title" ok-only @hide="resetInfoModal">
            <pre>{{ infoModal.content }}</pre>
          </b-modal>

          <b-modal ref="myModalRef" hide-footer title="Form title">
            <div class="d-block">
              <div class="frm text-right">
                <h4>This is form</h4>
                <div>{{sel}}</div>
                <button @click="on_do_add();">add</button>
                <button @click="on_close();">close</button>
              </div>
            </div>
          </b-modal>
        </div>

      </b-card-body>
    </b-card>
  </div>


  <!-- <div class="card card-outline-secondary">
    <div class="card-header">
      <h4 class="mb-0">This is BTable2</h4>
      <com-vs file-path-link="/src/views/BTable/BTableStart2.vue"></com-vs>
    </div>
    <div class="card-body">
    </div>
  </div> -->


</template>


<script>
export default {
  data() {
    return {
      items: [
        {id:1,ten:'Nguyễn Tấn Thức ',tuoi: 50},
        {id:2,ten:'Gia Cát Lượng',tuoi: 51},
        {id:3,ten:'Bùi Văn A',tuoi: 52},
        {id:4,ten:'Nguyễn Tấn C',tuoi: 53},
        {id:5,ten:'Trần Minh Chiến',tuoi: 54},
        {id:6,ten:'Tiết Đinh San',tuoi: 54},
        {id:7,ten:'Phàn Lê Huê',tuoi: 54},
        {id:8,ten:'Trần Đình Tuệ',tuoi: 54},
        {id:9,ten:'Khôi Nguyên',tuoi: 54},
      ],
      fields: [

      // { key: 'name', label: 'Person Full name', sortable: true, sortDirection: 'desc' },
      //       { key: 'age', label: 'Person age', sortable: true, class: 'text-center' },
      //       { key: 'isActive', label: 'is Active' },
      //       //{ key: 'actions', label: 'Actions' },
      //       { key: 'cmd', label: 'Cmd' },




        {key:'id',label:'Mã số',sortable:true, sortDirection:'desc'},
        {key:'ten',label:'Họ Tên',sortable:true},
        {key:'tuoi',label:'Tuổi',sortable:true},
        { key: "cmd", label: "Cmd" , class : 'text-right' }
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      infoModal: {
        id: "info-modal",
        title: "",
        content: ""
      },
      sel: {}
    };
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key };
        });
    }
  },
  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length;
  },
  methods: {
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`;
      this.infoModal.content = JSON.stringify(item, null, 2);
      this.$root.$emit("bv::show::modal", this.infoModal.id, button);
    },
    resetInfoModal() {
      this.infoModal.title = "";
      this.infoModal.content = "";
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    on_del(row) {
      let index = this.items.findIndex(x => x.id == row.item.id);
      this.items.splice(index, 1);
      this.totalRows = this.items.length;
    },
    on_edit(row) {
      this.sel = JSON.parse(JSON.stringify(row));
      this.$refs.myModalRef.show();
    },
    show_modal: function() {
      this.$refs.myModalRef.show();
    },
    on_close: function() {
      this.$refs.myModalRef.hide();
    }
  }
};
</script>


